$small-screen: 740px;
$medium-screen: 960px;
$easeInOutCubic: cubic-bezier(0.645, 0.045, 0.355, 1.000);

$textColorConstrast: rgba(#333030, 0.85);

.image{
    
    transition: opacity 1000ms $easeInOutCubic, filter 1000ms $easeInOutCubic;
    opacity:.6;
    img{
        border: 3px solid rgba(255,255,255,0.5);
        width: 100%;
        height: auto;
        transition: all 1000ms $easeInOutCubic;
    }
    &:hover {
        opacity:1;
        img{
            box-shadow: 0px 0px 50px 1px rgba(255,255,255,0.3);
        }
    }
    &.hidden{
        opacity: 0;
        filter: blur(20px);
        //transform:translateX(50px);
    }
}

#imageAladdin {
    align-self: center;
}

#imageEugene, #imageJeanLeon{
    width: 75%;
    margin: 3dvh auto;
}

#history {
    #history2paragraph {
        display: flex;
        align-items: end;
    }
}

#culture {
    .quote {
        h2 {
            width: 75%;
        }
        // .emphasis {
        //     text-decoration-line: underline;
        //     text-decoration-style: solid;
        //     text-decoration-thickness: 2px;
        //     text-underline-offset: 4px;
        //     text-decoration-color: rgba(255,255,255,0);
        // }

        .emphasis {
            background-color: rgba($color: #fff, $alpha: 0);
        }
    }
}

#beurette {
    .quote {
        display: flex;
        justify-content: center;
        h2 {
            width: 100%;
        }
    }
    .paragraph-30-70:has(.quote){
        gap: 3dvw;
    }
}

#analysis {
    p, h1, a{
        color: $textColorConstrast;
    }
    .read-more::after, .previous::after{
        background: $textColorConstrast;
    }
    .column-count-2 p{
        column-count: 2;
        column-gap: 3dvw;
    }
    #analysisTitle {
        margin-top: 5dvh;
    }
}

#men {
    #men1quote {
        width: 75%;
    }
}

#bibliography {
    ul {
        
    }
    li::marker {
        font-size: 6pt;
    }
    li {
        font-family: "Didot", serif;
        color:white;
        font-size: min(max(1vw, 12pt), 14pt);
        z-index: 1;
        line-height: min(max(2vw, 24pt), 28pt);
        transition: all 1000ms $easeInOutCubic;
        padding-left: 10px;

        &.hidden{
            opacity: 0;
            filter: blur(20px);
            transform: translateY(50px);
        }

        .italic {
            font-style: italic;
        }
    }
}


@media screen and (max-width: $small-screen) {
    .image{
        opacity:1;
        img{
            width: 100%;
            height: auto;
        }
    }

    #imageEugene, #imageJeanLeon{
        width: 100%;
    }

    #culture {
        .quote {
            h2 {
                width: 100%;
            }
        }
    }

    #imageAladdin {
        margin: 3dvh 0;
    }

    #analysis {
        .column-count-2 p{
            column-count: 1;
            column-gap: 0;
        }
    }
    #men {
        #men1quote {
            width: 100%;
        }
    }
}
